	import lvvPopup from '@/components/lvv-popup/lvv-popup.vue';
	import uniCountdown from "@/components/uni-countdown/uni-countdown.vue"
	import {
		get
	} from '@/config/db.js';
	import {
		apiBaseUrl
	} from '@/config/config.js';
	import share from '@/components/share/share.vue';




	import shareByWx from '@/components/share/shareByWx.vue'











	import htmlParser from '@/common/html-parser'
	export default {
		components: {
			lvvPopup,
			uniCountdown,
			share,




			shareByWx,










			// spec
		},
		data() {
			return {
				shareType: 3,
				providerList: [], // 分享通道 包含生成海报
				swiper: {
					indicatorDots: true,
					autoplay: true,
					interval: 3000,
					duration: 800,
				}, // 轮播图属性设置
				goodsInfo: [],
				teamInfo: [],
				favLogo: [
					'/static/image/ic-me-collect.png',
					'/static/image/ic-me-collect2.png'
				],
				horizontal: 'right', //右下角弹出按钮
				vertical: 'bottom',
				direction: 'vertical',
				pattern: {
					color: '#7A7E83',
					backgroundColor: '#fff',
					selectedColor: '#007AFF',
					buttonColor: "#FF7159"
				},

				query: '', // query参数登录跳转回来使用
				indicatorDots: false,
				autoplay: false,
				interval: 2000,
				duration: 500,
				lasttime: {
					day: 0,
					hour: 0,
					minute: 0,
					second: 0
				}, //购买倒计时
				userToken: 0,
				time: 0,
				order_id:'',//订单号
				orderInfo:{},
                shareUrl: '/pages/share/jump'
			}
		},
		onLoad(options) {
			if(options.order_id){
				this.order_id = options.order_id;
			}else{
				this.$common.errorToShow('参数错误');
			}
			let teamInfo,orderInfo,goodsInfo
	
			let pages = getCurrentPages()
			let pre = pages[pages.length - 2]
			if(typeof pre!='undefined'){




				

				teamInfo = pre.$vm.teamInfo
				orderInfo = pre.$vm.orderInfo

				




			}
			if(teamInfo && orderInfo){
				this.teamInfo = teamInfo;
				this.orderInfo = orderInfo;
				this.goodsInfo = orderInfo.items[0];
				
			}else{
				this.orderDetail();
				this.getTeam();
			}
			let timestamp = Date.parse(new Date())/1000;
			this.lasttime = this.$common.timeToDateObj(options.close_time-timestamp);
		},
		computed: {
			shareHref() {
				let pages = getCurrentPages()
				let page = pages[pages.length - 1]

				return apiBaseUrl + 'wap/' + page.route + '?scene=' + this.query;





			}
		},
		onReachBottom() {
			if (this.current === 2 && this.goodsComments.loadStatus === 'more') {
				this.getGoodsComments();
			}
		},
		methods: {
			//拼团信息
			getTeam(){
				this.$api.getOrderPintuanTeamInfo({order_id:this.order_id},res=>{
					if (res.status) {
						
					    this.teamInfo = {
					        list:res.data.teams,
							current_count:res.data.teams.length,
					        people_number:res.data.people_number,
					        team_nums:res.data.team_nums,//剩余
					        close_time:res.data.close_time,//关闭时间
							id:res.data.id,//拼团id
							team_id:res.data.team_id,//拼团团队id
							rule_id:res.data.rule_id,
					    };
						//console.log(this.lasttime);
					}else{
					    this.$common.errorToShow(res.msg)
					}
					
				});
			},
			//获取订单详情
			orderDetail () {
			    let _this = this
			    let data = {
			        order_id: _this.order_id
			    }
			    _this.$api.orderDetail(data, function(res) {
			        if (res.status) {
			            let data = res.data
						// 支付时间转换
						if (data.ctime !== null) {
							data.ctime = _this.$common.timeToDate(data.ctime)
						}
						
			            _this.orderInfo = data
						_this.goodsInfo = data.items[0];
					
			        } else {
			            _this.$common.errorToShow(res.msg)
			        }
			    })
			},
			// 关闭弹出层
			close() {
				this.$emit('close')
			},
			// 点击操作
			clickHandler(e) {
				if (e.cate === 'poster') {
					this.createPoster()
				} else {
					// 去分享
					this.share(e)
				}
			},
			// 显示modal弹出框
			toshow(type, team_id = 0) {
				if (type == 1) {
					this.lvvpopref_type = 1;
				}
				if (team_id !== 0) {
					this.team_id = team_id;
				}
				this.$refs.lvvpopref.show();
			},
			// 关闭modal弹出框
			toclose() {
				this.$refs.lvvpopref.close();
			},
			// 跳转到h5分享页面
			goShare() {
				this.$refs.share.show();
			},
			closeShare() {
				this.$refs.share.close();
			},
            //获取分享URL
            getShareUrl() {
                let data = {
                    client: 2,
                    url: "/pages/share/jump",
                    type: 1,
                    page: 3,
                    params: {
                        goods_id: this.goodsInfo.goods_id,
                        team_id: this.teamInfo.list[0].team_id
                    }
                };
                let userToken = this.$db.get('userToken');
                if (userToken && userToken != '') {
                	data['token'] = userToken;
                }
                this.$api.share(data, res => {
                    this.shareUrl = res.data
                });
            }
		},
        watch:{
            goodsInfo: {
                handler () {
                    this.getShareUrl();
                },
                deep: true
            },
            teamInfo: {
                handler () {
                    this.getShareUrl();
                },
                deep: true
            }
        },
		//分享
		onShareAppMessage() {
            return {
			 	title: this.goodsInfo.name,



			 	imageUrl: this.goodsInfo.image_url,
			 	path: this.shareUrl
			 }
		 }
	}
